import { BaseModel } from "./MainModel";

export default class ManModel extends BaseModel {

    async getMan(data) {
        return this.directFetch({
            url: "Man",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async getManById(data) {
        return this.directFetch({
            url: "ManDetail",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async getManBySearch(data) {
        return this.directFetch({
            url: "SearchMan",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async insertManDetail(data) {
        this.directFetch({
            url: "InsertMan",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async updateManDetail(data) {
        //console.log("update:",data);
        this.directFetch({
            url: "UpdateMan",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async resignMan(data) {
        this.directFetch({
            url: "ResignMan",
            method: "POST",
            body: JSON.stringify(data),
        });
    }

    async getResignMan() {
        return this.directFetch({
            url: "ManRe",
            mathod: "GET",
        });
    }

 

    async getPosition(){
        return this.directFetch({
            url: "GetPosition",
            mathod: "GET",
           // body: JSON.stringify(data),
        });
    }

   

}